// Filter
.filter-list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  list-style: none; }

// General page layout
.portfolio-page-layout {
  width: 85%;
  margin: 10px auto; }
.page-title-container {
  display: flex;
  justify-content: center;
  padding: 10px 0;
  h1 {
    text-align: center; } }
h1.page-title {
  padding: 20px; }
h2.piece-subtitle {
  text-align: center; }
//
.page-content-container {
  display: block; }
//
.image-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  .image-side-by-side {
    display: flex;
    flex-direction: row;
    img {
      padding: 3px; } }
  img {
    padding: 3px;
    width: 100%;
    height: auto; } }
p {
  text-align: center; }
//
.piece-description {
  display: block;
  justify-content: center;
  align-items: center;
  margin: 1.5em auto;
  padding: 1em;
  border-radius: 1em;
  max-width: 900px;
  background: {
    color: $container-color; }
  p {
    text-align: center;
    font-size: 15px;
    a {
      font-weight: bold;
      color: #FE9920; } } }
.piece-tag {
  padding-top: 1em;
  display: flex;
  flex-flow: wrap row;
  justify-content: center;
  li {
    padding: 3px 5px;
    border-radius: 5px;
    margin: 5px;
    border: 2px solid $white;
    list-style: none; } }

@media screen and (max-width: 1300px) {
  .image-container {
    .image-side-by-side {
      flex-direction: column; } } }

.video-container {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  margin: 10px;
  iframe {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    border: 0; } }
p.video-description {
  text-align: center;
  a {
    color: $nav-color;
    font-weight: bold; }
  a:hover {
    color: $white; } }
//
.media-w-text.smaller-size {
  max-width: 600px;
  margin: 0 auto; }
.media-w-text.smaller-size-2 {
  max-width: 900px;
  margin: 0 auto; }
