// Removing basic elements
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  transition: none; }

// MAIN HEADER
.nav-header {
  // Header positioning
  z-index: 110;
  position: fixed;
  top: 0;
  left: 0;
  width: $nav-width;
  height: 100%;
  //
  background-color: $nav-color;
  // transitions
  transition: width 0.1s;
  /* WEBSITE LOGO */
  .nav-logo {
    display: flex;
    justify-content: center;
    padding: 20px;
    margin: 10px;
    img {
      width: 70px;
      height: auto; }
    img:hover {
      animation: rotatelogo 2s infinite ease-in-out;
      @keyframes rotatelogo {
        to {
          transform: rotateY(360deg); } } } } }
// scroll-hide
.nav-header, .profile-container-open {
  -ms-overflow-style: none;
  scrollbar-width: none;
  overflow-y : auto;
  -webkit-overflow-scrolling: touch; }
// chrome
.nav-header::-webkit-scrollbar, .profile-container-open::-webkit-scrollbar {
  display: none; }

/* NAVIGATION BAR */
.nav-bar {
  display: none;
  justify-content: center; }
// Mobile nav icon
.nav-icon-open-wrapper {
  display: none;
  cursor: pointer; }
.nav-icon-open-wrapper {
  div {
    height: 4px;
    margin: 5px;
    width: 27px;
    background-color: $text-color;
    transition: all 0.3s ease; } }
.toggle .line1 {
  transform: translate(0px, 9px) rotate(45deg); }
.toggle .line2 {
  opacity: 0; }
.toggle .line3 {
  transform: translate(-0px, -9px) rotate(-45deg); }

.profile-container-open {
  z-index: 90;
  background-color: $container-open-color;
  position: fixed;
  padding: 10px;
  align-items: center;
  justify-content: center;
  padding-top: 94px;
  width: 100vw;
  height: 100vh;
  transform: translateY(-200%); }
// Switch to mobile navigation
@media screen and (max-width: 1200px) {
  .nav-header {
    width: $nav-width-tab;
    .nav-logo {
      padding: 0px;
      margin: 10px; } } }
@media screen and (max-width: $switch-to-mobile), screen and (max-height: 425px) {
  .profile-container {
    display: none; }
  .nav-header {
    z-index: 100;
    transition: none;
    overflow-y: hidden;
    // Top header
    width: 100%;
    height: auto;
    justify-content: center;
    .nav-logo {
      padding-top: 0; } }
  .nav-bar {
    display: flex;
    margin: auto; }
  .nav-icon-open-wrapper {
    display: block;
    position: fixed;
    top: 1.5em;
    left: 1.5em; }
  html.nav-active {
    body {
      // overflow: hidden
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0; }
    // .nav-body-overlay
    //   opacity: 1
    //   transition: opacity 0.2s ease-in-out, transform 0s
    //   transform: translateX(0)
    .profile-container-open {
      // display: flex
      // flex-direction: column
      transition: transform 0.6s ease;
      transform: translateX(0); } } }
